<template>
    <div class='dashboard'>
        <div class='toolbar'>
            <img class='toolbar__logo' src='../assets/logo-horizontal.png'/>
            <div class='toolbar__title'>SESSION CONFIGURATION</div>
        </div>
        <!--<dashboard-session/>-->
        <div class='content'>
            <div class='form'>
                <div class='form__title form__title--initial'>
                        Initial Settings
                </div>
                <div class='form__section'>
                    <div class='form__initial'>
                        <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='baselineModel' :label="'BASELINE MODEL'"/>
                        <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='baselineModel' :label="'LOCATION'"/>
                        <div class='form__inline'>
                            <custom-toggle v-model='baselineModel' :label="'PERMANENT STAY'"/>
                            <custom-integer :label="'Duration'" :maxValue="500" style='justify-self:flex-end;margin-top:auto'/>
                            <custom-select :options="[{'value':'durationHour', 'text':'Hours'},{'value':'durationYear', 'text':'Years'}]" v-model='baselineModel' :label="'TIME'"/>
                        </div>
                    </div>
                </div>
                <div class='form__title form__title--astronauts'>
                        Astronauts
                </div>
                <div class='form__section'>
                    <div class='form__initial'>
                        <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='baselineModel' :label="'GREENHOUSE'"/>
                        <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='baselineModel' :label="'GREENHOUSE'"/>
                        <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='baselineModel' :label="'BASELINE MODEL'"/>
                        <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='baselineModel' :label="'LOCATION'"/>                    </div>
                </div>
                <div class='form__title form__title--greenhouse'>
                        Astronauts
                </div>
                <div class='form__section'>
                    <div class='form__initial'>
                        <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='baselineModel' :label="'CREW QUARTERS'"/>
                        <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='baselineModel' :label="'GREENHOUSE'"/>
                        <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='baselineModel' :label="'BASELINE MODEL'"/>
                        <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='baselineModel' :label="'LOCATION'"/>                    </div>
                </div>
                <div class='form__title form__title--power'>
                        POWER
                </div>
                <div class='form__section'>
                    <div class='form__initial'>
                        <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='baselineModel' :label="'BASELINE MODEL'"/>                        <div class='form__inline'>
                            <custom-toggle v-model='baselineModel' :label="'PERMANENT STAY'"/>
                            <custom-integer :label="'Duration (SOLS)'" :maxValue="500"/>
                        </div>
                    </div>
                </div>
                <div style='margin-top: auto; display: flex; justify-content: center; align-items: center;'>
                    <input type='button' value='submit' style='margin:8px; '/>
                </div>
            </div>    
            <div class='wiki'></div>
            <div class='feedback__mass'></div>
            <div class='feedback__power'></div>
        </div>
    </div>
</template>

<script>
import Select from '@/components/InputSelect'
import Integer from '@/components/InputInteger'
import Toggle from '@/components/InputToggle'
import Session from '@/components/DashboardSession'
import axios from 'axios'
axios.defaults.withCredentials = true;
export default {

    components:{
        'custom-select':Select,
        'custom-integer':Integer,
        'custom-toggle':Toggle,
        'dashboard-session':Session,
    },

    data(){
        return{
            baselineModel:'',

            //DELETE THIS!
            water: "",
            co2:"",
            oxygen:"",
            updateRunning:true,
            updateIntervalID:'',
            updateInterval:1000,
            currentStep:0
        }
    },

     mounted: function(){
        this.setUpdateTimeout()
        console.log("TESTING")
     },

     methods:{
         getStep:function(){             
             const stepSize = 1
             const path= 'http://localhost:8000/get_step'

            if(this.currentStep != null){
                this.currentStep += stepSize
                this.path += '?step_num=' + this.currentStep;                
            }

            axios.get('http://localhost:8000/get_step?step_num='+this.currentStep)
            .then(response =>{
                this.message = 'New Game Started'
                this.oxygen = response.data.agents.total_production.atmo_h2o
                this.co2 = response.data.agents.avg_carbon_dioxide_pressure
                this.water = response.data.agents.total_water
                console.log(response.data)
                //this.setUpdateTimeout()
             })
         },

        setUpdateTimeout:function(){
            this.updateIntervalID = setTimeout(() => {
                if(this.updateRunning){
                    this.getStep()
                }
            },1000)
        }
     }

}
</script>

<style lang="scss" scoped>
    @import '../scss/_theme';

    .form{
        display: flex;
        flex-direction:column;
    }

    .form__inline{
        display: grid;
        grid-template-columns:repeat(auto-fit,minmax(0px,auto));
        grid-column-gap: 16px;
    }

    .form__title{
        @extend %font-panel-title;
        margin:auto auto auto 0px;
    }

    .form__title--initial{
        margin-top: 0px;
        grid-area: initial;
    }

    .form__title--astronauts{
        grid-area: astronauts;
    }

    .form__title--greenhouse{
        grid-area: greenhouse;
    }

    .form__title--power{
        grid-area: power;
    }

    .form__title::after{
        display:block;
        margin: 8px 0px;
        border-bottom: $border-standard-width $border-standard-style $border-standard-color;
        content:'';
    }

    .form__section{
        margin-left: 32px;
    }

    .input__field{
        position:relative;

        .test__field{
            border: 1px solid grey;
            background-color: transparent;
            padding: 10px 4px 2px 12px;
            box-sizing: border-box;
            color:white;
        }

        .test__label{
            @extend %font-text;
            position: absolute;
            color: grey;
            font-size: 1rem;
            left: 12px;
            top: 25%;
            background-color:transparent;
        }

        .test__label--active{
            color: grey;
            padding: 0px 2px;
            background-color:#1d1f20;
            font-size: .65rem;
            left: 12px;
            top: -3px;
        }

        .test__placeholder{
            color: white;
        }
    }

    .test__label{
        /*transition: left .1s ease-in-out;
        transition: top .1s ease-in-out;
        transition: right .1s ease-in-out;
        transition: bottom .1s ease-in-out;
        transition: font-size .1s ease-in-out;*/
        transition: all .1s, color 0s;
    }

    .input__field:hover{
        .test__field{
            border: 1px solid white;
        }

        .test__label{
            background-color: #1d1f20;
            color: white;
        }
    }

    .input__field:focus-within{
        .test__field{
            outline: none;
            border: 1px solid #00aaff;
        }

        .test__label{
            color: #00aaff;
            background-color:#1d1f20;
            font-size: .65rem;
            left: 12px;
            top: -3px;
            padding: 0px 2px;
        }
    }

    .dashboard{
        width: 100%;
        height: 100%;
        display: grid;
        grid-template-columns: minmax(192px, 15vw) 1fr;
        grid-column-gap: 32px;
        grid-template-rows: 1fr;
        grid-template-areas:'dashboard__toolbar dashboard__content';
    }

    .toolbar{
        @extend %display-panel;
        display: flex;
        flex-direction:column;
        justify-content:flex-start;
        align-items: center;
    }

    .toolbar__logo{
        width: 100%;
        height: auto;
    }

    .toolbar__title{
        @extend %font-sub-title;
        margin: 16px auto;
        text-align: center;
    }

    .toolbar__title::after{
        display:block;
        margin: 8px 0px;
        border-bottom: $border-standard-width $border-standard-style $border-standard-color;
        content:'';
    }

    .content{
        display: grid;
        grid-template-columns: 1.5fr .75fr .75fr;
        grid-template-rows: 2fr 1fr;
        grid-column-gap: 8px;
        grid-row-gap: 8px;
        grid-template-areas:
        'content__configuration content__wiki content__wiki'
        'content__configuration content__mass content__power';

        .form{
            @extend %display-panel;
            grid-area:content__configuration;
        }

        .wiki{
            @extend %display-panel;
            grid-area:content__wiki;
        }

        .feedback__mass{
            @extend %display-panel;
            grid-area:content__mass;
        }

        .feedback__power{
            @extend %display-panel;
            grid-area:content__power;
        }
    }

</style>
