<template>
    <div class='input input--select'>
        <label class='toggle__label'>
            <input ref='checkbox' type='checkbox' class='toggle__field' :id='label' v-model='selected' @input='updateSelect()'/>
            <span class='toggle__slider'></span>
        </label>
        <span class='input__label--toggle' :class="(!selected ? '' : 'input__label--toggle--active')">{{label}}</span>
    </div>
</template>

<script>
export default {
    props:['value','options','label'],

    methods:{
        updateSelect(){
            this.$emit('input',this.$refs.selectValue.value)
        }
    },

    data(){
        return{
            selected:false,
        }
    }

}
</script>

<style lang='scss' scoped>
    @import '../scss/_theme';
    
    .input{
        display: flex;
        align-items: center;
    }

    .toggle__label{
        position: relative;
        display:inline-block;
        width: 36px;
        height: 20px;

        input{
            opacity: 0;
            width: 0;
            height: 0;
        } 
    }

    .toggle__slider{
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: grey;
        transition: .4s;
    }
    
    .toggle__slider::before{
        position: absolute;
        content: "";
        height: 20px;
        width: 20px;
        left: 0px;
        bottom: 0px;
        background-color: white;
        transition: .4s;
    }

    input:checked + .toggle__slider{
        background-color: #00aaff;
    }

    .input:focus + .toggle__slider{
        box-shadow: 0 0 1px #00aaff;
    }

    input:checked + .toggle__slider::before{
        transform: translateX(16px);
    }

    .toggle__slider{
        border-radius: 28px;
    }

    .toggle__slider::before{
        border-radius: 50%;
    }

    .input__label--toggle{
        @extend %font-text;
        margin: 8px;
        color: grey;
        font-size: .75rem;
        font-weight: 300;
    }

    .input__label--toggle--active{
        color: #00aaff;
        font-weight: 900;
    }




</style>
