<template>
    <div class='chart__wrapper'>
        <canvas :id='chartID' style='height: 100%; width: 100%;'/>
    </div>
</template>

<script>
import Chart from 'chart.js';
export default {
    props:['chartID','chartLabels','chartValues','chartType','chartColors','displayLegend'],

    data(){
        return{
            chart:'',
        }
    },

    computed:{
        updateChart:function(){
            let chartData = this.chartValues
            return chartData 
        }
    },

    watch:{
        updateChart: function(){
            this.chart.data.datasets[0].data.pop()
            this.chart.data.datasets[0].data = this.updateChart
            this.chart.update()
        }
    },

    mounted: function(){
    const ctx = document.getElementById(this.chartID);
    this.chart = new Chart(ctx,{
      type: this.chartType,
      data:{
        labels: this.chartLabels,
        datasets:[{
          backgroundColor:this.chartColors,
          data:this.chartValues,
        }]
      },
      rotation: Math.PI * -.5,
      options:{
          legend:{
              display: this.displayLegend
          },
          responsive: true,
          maintainAspectRatio: true,
          drawborder: false,

        rotation: Math.PI,
        circumference: 1 * Math.PI,
      },
    })  
  }
}
</script>

<style>
    .chart__wrapper, canvas{
        width:100%;
        height:100%;
    }
</style>
