export default{
    state:{
        entryContentComponent:'EntryLogin',
        entryContentMessage:'WELCOME',
    },

    mutations:{
        SETCONTENTTOLOGIN(state){
            state.entryContentComponent='EntryLogin'
            state.entryContentMessage='WELCOME'
        },

        SETCONTENTTOREGISTRATION(state){
 
            state.entryContentComponent='EntryRegistration'
            state.entryContentMessage='REGISTRATION'
        },

        SETCONTENTTOOPTIONS(state){
            state.entryContentComponent='EntryOptions'
            state.entryContentMessage='MAIN MENU'
        }
    }
}