<template>
    <div class='input input--select'>
        <label :for='label' class='input__label' :class="(selected==0? '' : 'input__label--active')">{{label}}</label>
        <input ref='integerValue' type='number' class='input__field' :id='label' v-model='selected' :max='maxValue' :min='minValue' :step='stepValue' @input='updateSelect()'>
    </div>
</template>

<script>
export default {
    props:['value','options','label','minValue','maxValue','stepValue'],

    methods:{
        updateSelect(){
            this.$emit('input',this.$refs.integerValue.value)
        }
    },

    data(){
        return{
            selected:0,
        }
    }

}
</script>

<style lang='scss' scoped>
    @import '../scss/_theme';
    .input{
        position: relative;
        height: auto;
        width: auto;
        margin-bottom:12px;

        .input__field{
            border: 1px solid grey;
            background-color: transparent;
            padding: 10px 4px 2px 12px;
            box-sizing: border-box;
            color: white;
            font-size: .75rem;
            width: 96px;
        }

        .input__label{
            @extend %font-text;
            color: grey;
            position: absolute;
            padding: 0px 2px;
            background-color: #1d1f20;
            font-size: .65rem;
            left:4px;
            top: -3px;
        }

        .input__label--active{
            color: #00aaff;
        }
    }

    .input:hover{
        .input__field{
            border: 1px solid white;
        }

        .input__label{
            background-color: #1d1f20;
            color: white;
        }
    }

    .input:focus-within{
        .input__field{
            outline:none;
            border: 1px solid #00aaff;
        }

        .input__label{
            color: #00aaff;
            background-color: #1d1f20;
            font-size: .65rem;
            left: 4px;
            top: -3px;
            padding: 0px 2px;
        }
    }
    
    option{
        color: black;
    }


</style>
