<template>
    <div class='input input--select'>
        <label :for='label' class='input__label' :class="(selected===''? '' : 'input__label--active')">{{label}}</label>
        <select  ref='selectValue' class='input__field' :id='label' v-model='selected' @input='updateSelect()'>
            <option class='select__option' :value='value.value' v-for='value in options' :key='value.text'>
                {{value.text}}
            </option>
        </select>
    </div>
</template>

<script>
export default {
    props:['value','options','label'],

    methods:{
        updateSelect(){
            this.$emit('input',this.$refs.selectValue.value)
        }
    },

    data(){
        return{
            selected:'',
        }
    }

}
</script>

<style lang='scss' scoped>
    @import '../scss/_theme';
    .input{
        position: relative;
        height: auto;
        width: auto;
        margin-bottom:12px;

        .input__field{
            border: 1px solid grey;
            background-color: transparent;
            padding: 8px 4px 2px 10px;
            box-sizing: border-box;
            color: white;
            font-size: .75rem;
        }

        .input__label{
            @extend %font-text;
            position: absolute;
            color: grey;
            font-size: .9rem;
            left: 12px;
            top:25%;
            background-color: transparent;
            transition: all .1s, color 0s;
        }

        .input__label--active{
            color: #00aaff;
            padding: 0px 2px;
            background-color: #1d1f20;
            font-size: .6rem;
            left:12px;
            top: -3px;
        }
    }

    .input:hover{
        .input__field{
            border: 1px solid white;
            color: white;
        }

        .input__label{
            background-color: #1d1f20;
            color: white;
        }
    }

    .input:focus-within{
        .input__field{
            outline:none;
            color:white;
            border: 1px solid #00aaff;
        }

        .input__label{
            color: #00aaff;
            background-color: #1d1f20;
            font-size: .6rem;
            left: 12px;
            top: -3px;
            padding: 0px 2px;
        }
    }
    
    option{
        color: black;
    }


</style>
