<template>
    <div class='login'>
        <div class='input--icon'>
            <fa-icon class='fa-icon' transform='grow-8' :icon="['fal','user-circle']"/>
            <label for='username__textfield' class='input__label' v-bind:class="(usernameField==='' ? '' : 'input__label--active')">USERNAME</label>
            <input type='text' class='input__textfield' id='username__textfield' v-model='usernameField' placeholder='Username'/>
        </div>
        <div class='input--icon'>
            <fa-icon class='fa-icon' transform='grow-8' :icon="['fal','lock-alt']"/>
            <label for='password__textfield' class='input__label' v-bind:class="(passwordField==='' ? '' : 'input__label--active')">PASSWORD</label>
            <input type='password' class='input__textfield' id='password__textfield' v-model='passwordField' placeholder='Password'/>
        </div>
        <input type='button' class='login__btn' value='Launch' v-on:click='login'/>
        <div class='login__navigation'>
            <a class='navigation__link' href='#' v-on:click='mutateContentToRegistration'>CREATE ACCOUNT</a>
            <a class='navigation__link'>NEED HELP?</a>
        </div>
    </div>
</template>

<script>
import {mapState,mapMutations} from 'vuex'
import axios from 'axios'
axios.defaults.withCredentials = true;
export default {

    data(){
        return{
            usernameField:'',
            passwordField:'',
        }
    },

    methods:{
        ...mapMutations(['SETCONTENTTOREGISTRATION']),

        mutateContentToRegistration: function(){
            console.log("TEST")
            this.SETCONTENTTOREGISTRATION()
        },

        isEmpty: function(value){
            return (value === null || value === '')
        },

        //DELETE! This needs to be changed later to handle actual routes instead of just logging in.
        login:function(){
            const path = window.location //'http://localhost:8000/'

            //Verify Username and Password aren't empty
            if(this.isEmpty(this.usernameField) || this.isEmpty(this.passwordField)){
                console.log("YOU FORGOT SOMETHING!")
                return null
            }

            axios.post(path + '/loginvue',{
                username:this.usernameField,
                password:this.passwordField,
            }).then(response=>{
                if(response.status == 200 ){
                    console.log('successful login')
                    this.newgame()
                }
            }).catch(error=>{
                console.log(error.response.status)
            })            
        },

        //DELETE! This needs to be changed later to handle actual routes instead of just logging in.
        newgame:function(){
            console.log("TEST")
            const path = window.location//'http://localhost:8000'
            axios.post(path + '/new_game_vue',{

            }).then(response=>{
                console.log('New Game Started')
            })
        }
    }
}
</script>

<style lang="scss" scoped>
    @import '../scss/_theme';
    @import '../scss/_entryContent';

    .login{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction:column;
        justify-content: flex-start;
        align-items: center;
    }

    .input--icon{
        @extend %entry__wrapper--sm;
        @extend %input__wrapper--icon;
    }

    .input__textfield{
        @extend %input__textfield;
    }

    *:focus{
        outline: none;
    }

    .login__btn{        
        @extend %entry__wrapper--md;
        @extend %entry__submitbtn;
    }

    .login__btn:hover{
        background-color:#ea3b3b;
    }

    .login__navigation{
        @extend %entry__nav--underline;
        @extend %entry__nav;

        a{
            color: white;
        }
    }

</style>
