<template>
    <div class='entry'> 
        <img class='entry__logo entry__wrapper--lg' src='../assets/logo-vertical.png'/>
        <div class='entry__title entry__wrapper--sm'>{{entryContentMessage}}</div>
        <component :is='entryContentComponent'/>
    </div>
</template>

<script>
import EntryLogin from '@/components/EntryLogin'
import EntryRegistration from '@/components/EntryRegistration'
import {mapState,mapMutations} from 'vuex'
export default {
    methods:{
        ...mapMutations(['SETCONTENTTOLOGIN']),
    },
    
    mounted(){
        this.SETCONTENTTOLOGIN()    //This will always default to the login component when the user returns to this screen 
    },

    computed:{
        ...mapState({
            entryContentComponent: state => state.entry.entryContentComponent,
            entryContentMessage: state => state.entry.entryContentMessage,
        })
    },

    components:{
        'EntryLogin':EntryLogin,
        'EntryRegistration':EntryRegistration,
        
    }
}
</script>

<style lang="scss" scoped>
    @import '../scss/_theme';
    
    .entry{
        
        @extend %display-panel;
        padding:16px 32px 16px 32px;
        width: 400px;
        height: 640px;
    
        display: flex;
        flex-direction:column;
        justify-content: flex-start;
        align-items: center;
    }


    .entry__wrapper--none{ 
        width: 100%;
    }

    .entry__wrapper--sm{
        margin-bottom: 16px; 
        width: 100%;
    }

    .entry__wrapper--md{
        margin-bottom: 32px; 
        width: 100%;
    }

    .entry__wrapper--lg{
        margin-bottom: 48px; 
        width: 100%;
    }

    .entry__logo{
        width: 50%;
        height: auto;
    }

    .entry__title{
        @extend %font-title;
        text-align:center;
        border-bottom: $border-standard-width $border-standard-style $border-standard-color;   
        padding-bottom: 8px;     
    }

    .input__wrapper--icon{
        display: grid;
        grid-template-columns: 32px 1fr;
        justify-content: center;
        align-items: center;
        background-color: #484849;
        border:none; 
        width: 100%;
        font-size: 1rem;
        padding: 12px;
        height: auto;
        border-radius: 10px;       
        box-sizing: border-box;
        color: white;
    }

    .entry__textfield{
        width: 100%;
        background-color: transparent;
        border: none;
        font-size:1.25rem;
        color: white;
    }

    .entry__submitbtn{
        margin-top: auto;
        border: 2px solid #ea3b3b;
        background-color: transparent;
        width: 100%;
        font-size: 1.5rem;
        padding: 8px;
        border-radius: 10px;
        box-sizing: border-box;
        color: white;
        font-weight: 600;
        width: 75%;
        transition: background-color .2s ease-in-out;
    }

    .entry__submitbtn:hover{
        background-color:#ea3b3b;
    }

    .entry__navigation{
        @extend %font-text;
        display: flex; 
        justify-content:space-between; 
        color: white;
        text-decoration: underline;
    }
    
    ::placeholder{
        color: lightgray;
    }

    *:focus{
        outline: none;
    }
</style>
