import Vue from 'vue';
import Router from 'vue-router';
import Entry from './views/Entry';
import Dashboard from './views/Dashboard';


Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'entry',
      component: Entry,
    },
    {
      path: '/dashboard',
      name: 'dashboard',
      component: Dashboard,
    },    
  ],
});
